import React from "react";
import { Button,} from "antd";
import "../styles/css/record.scss";

let mediaSource = new MediaSource();

let mediaRecorder;
let recordedBlobs;
let sourceBuffer;

let recordedVideo;

class Record extends React.Component {
  constructor() {
    super();
    this.state = {
      status:'start',
    }
  }

  componentDidMount() {
    recordedVideo = this.refs['recordedVideo'];
    mediaSource.addEventListener('sourceopen', this.handleSourceOpen, false);
  }

  handleSourceOpen = (event) => {
    console.log('MediaSource opened');
    sourceBuffer = mediaSource.addSourceBuffer('video/webm; codecs="vp8"');
    console.log('Source buffer: ', sourceBuffer);
  }

  handleSuccess = (stream) => {
    this.setState({
      status:'startRecord',
    });
    console.log('getUserMedia() got stream:', stream);
    window.stream = stream;

    const recordVideo = this.refs['recordVideo'];
    recordVideo.srcObject = stream;
  }

  startClickHandler = async (e) => {
    //是否回音抑制处理
    const hasEchoCancellation = false;
    const constraints = {
      audio: {
        echoCancellation: { exact: hasEchoCancellation }
      },
      video: {
        width: 1280, height: 720
      }
    };
    console.log('Using media constraints:', constraints);
    await this.init(constraints);
  }

  init = async (constraints) => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia(constraints);
      this.handleSuccess(stream);
    } catch (e) {
      console.error('navigator.getUserMedia error:', e);
    }
  }

  startRecordButtonClickHandler = (e) => {
      this.startRecording();
      this.setState({
        status:'stopRecord',
      });
  }

  stopRecordButtonClickHandler = (e) => {
      this.stopRecording();
      this.setState({
        status: 'play',
      });
  }

  playButtonClickHandler = (e) => {
    const superBuffer = new Blob(recordedBlobs, { type: 'video/webm' });
    recordedVideo.src = null;
    recordedVideo.srcObject = null;
    recordedVideo.src = window.URL.createObjectURL(superBuffer);
    recordedVideo.controls = true;
    recordedVideo.play();
    this.setState({
      status:'download',
    });
  }

  downloadButtonClickHandler = (e) => {
    const blob = new Blob(recordedBlobs, { type: 'video/webm' });
    const url = window.URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.style.display = 'none';
    a.href = url;
    a.download = 'test.webm';
    document.body.appendChild(a);
    a.click();
    setTimeout(() => {
      document.body.removeChild(a);
      window.URL.revokeObjectURL(url);
    }, 100);

    this.setState({
      status:'start',
    });
  }

  handleDataAvailable = (event) => {
    console.log('handleDataAvailable', event);
    if (event.data && event.data.size > 0) {
      recordedBlobs.push(event.data);
    }
  }

  startRecording = () => {
    recordedBlobs = [];
    let options = { mimeType: 'video/webm;codecs=vp9' };
    if (!MediaRecorder.isTypeSupported(options.mimeType)) {
      console.error(`${options.mimeType} is not Supported`);
      options = { mimeType: 'video/webm;codecs=vp8' };
      if (!MediaRecorder.isTypeSupported(options.mimeType)) {
        console.error(`${options.mimeType} is not Supported`);
        options = { mimeType: 'video/webm' };
        if (!MediaRecorder.isTypeSupported(options.mimeType)) {
          console.error(`${options.mimeType} is not Supported`);
          options = { mimeType: '' };
        }
      }
    }

    try {
      mediaRecorder = new MediaRecorder(window.stream, options);
    } catch (e) {
      console.error('Exception while creating MediaRecorder:', e);
      return;
    }

    console.log('Created MediaRecorder', mediaRecorder, 'with options', options);
    mediaRecorder.onstop = (event) => {
      console.log('Recorder stopped: ', event);
      console.log('Recorded Blobs: ', recordedBlobs);
    };
    mediaRecorder.ondataavailable = this.handleDataAvailable;
    mediaRecorder.start(10); // collect 10ms of data
    console.log('MediaRecorder started', mediaRecorder);
  }

  stopRecording = () => {
    mediaRecorder.stop();
  }

  render() {

    return (
      <div className="container">

        <h1>
          <span>音视频录制</span>
        </h1>

        <video className="small-video" ref="recordVideo" playsInline autoPlay muted></video>
        <video className="small-video" ref="recordedVideo" playsInline loop></video>

        <div>
          <Button
            className="button"
            onClick={this.startClickHandler}
            disabled={this.state.status != 'start'}>
            打开摄像头
            </Button>
          <Button
            className="button"
            disabled={this.state.status != 'startRecord'}
            onClick={this.startRecordButtonClickHandler}>
            开始录制
          </Button>
          <Button
            className="button"
            disabled={this.state.status != 'stopRecord'}
            onClick={this.stopRecordButtonClickHandler}>
            停止录制
          </Button>
          <Button
            className="button"
            disabled={this.state.status != 'play'}
            onClick={this.playButtonClickHandler}>
            播放
          </Button>
          <Button
            className="button"
            disabled={this.state.status != 'download'}
            onClick={this.downloadButtonClickHandler}>
            下载
            </Button>
        </div>
      </div>
    );
  }
}

export default Record;
