import React from "react";
/**
 * 麦克风示例
 */
class Microphone extends React.Component {

  componentDidMount(){
    const constraints = window.constraints = {
      audio: true,
      video: false
    };
    navigator.mediaDevices.getUserMedia(constraints).then(this.handleSuccess).catch(this.handleError);
  }

  handleSuccess = (stream) => {

    let audio = this.refs['audio'];

    const audioTracks = stream.getAudioTracks();
    console.log('Got stream with constraints:', constraints);
    console.log('获取的音频设备为: ' + audioTracks[0].label);
    stream.oninactive = () => {
      console.log('Stream ended');
    };
    window.stream = stream;
    //将audio播放源指定为stream
    audio.srcObject = stream;
  }

  //错误处理
  handleError(error) {
    console.log('navigator.MediaDevices.getUserMedia error: ', error.message, error.name);
  }
  

  render() {
    
    return (

      <div className="container">

        <h1>
          <span>麦克风示例</span>
        </h1>

        <audio ref="audio" controls autoPlay></audio>
              
        <p class="warning">警告: 如何没有使用头戴式耳机, 声音会反馈到扬声器.</p>

      </div>

    );
  }
}

export default Microphone;
