import React from "react";
import { Layout, Button, Modal, Icon, notification, Card, Spin, Tooltip } from "antd";
const { confirm } = Modal;
const { Header, Content, Footer, Sider } = Layout;


let startButton;// = document.getElementById('startButton');
let callButton;// = document.getElementById('callButton');
let hangupButton;// = document.getElementById('hangupButton');
// callButton.disabled = true;
// hangupButton.disabled = true;
// startButton.addEventListener('click', start);
// callButton.addEventListener('click', call);
// hangupButton.addEventListener('click', hangup);


let startTime;
let localVideo;// = document.getElementById('localVideo');
let remoteVideo;// = document.getElementById('remoteVideo');

let localStream;
let pc1;
let pc2;
const offerOptions = {
  offerToReceiveAudio: 1,
  offerToReceiveVideo: 1
};


class PeerConnection extends React.Component {
  constructor() {
    super();
  
  }

  componentDidMount(){
    startButton = this.refs['startButton'];
    callButton = this.refs['callButton'];
    hangupButton = this.refs['hangupButton'];

    localVideo = this.refs['localVideo'];
    remoteVideo = this.refs['remoteVideo'];
   
    localVideo.addEventListener('loadedmetadata', () => {
        console.log(`Local video videoWidth: ${this.videoWidth}px,  videoHeight: ${this.videoHeight}px`);
      });

      remoteVideo.addEventListener('loadedmetadata', ()=> {
        console.log(`Remote video videoWidth: ${this.videoWidth}px,  videoHeight: ${this.videoHeight}px`);
      });
      
      remoteVideo.addEventListener('resize', () => {
        console.log(`Remote video size changed to ${remoteVideo.videoWidth}x${remoteVideo.videoHeight}`);
        // We'll use the first onsize callback as an indication that video has started
        // playing out.
        if (startTime) {
          const elapsedTime = window.performance.now() - startTime;
          console.log('Setup time: ' + elapsedTime.toFixed(3) + 'ms');
          startTime = null;
        }
      });  

  }


getName = (pc) => {
  return (pc === pc1) ? 'pc1' : 'pc2';
}

getOtherPc = (pc) => {
  return (pc === pc1) ? pc2 : pc1;
}

start = async () => {
  console.log('Requesting local stream');
//   startButton.disabled = true;
  try {
    const stream = await navigator.mediaDevices.getUserMedia({audio: true, video: true});
    console.log('Received local stream');
    localVideo.srcObject = stream;
    localStream = stream;
    // callButton.disabled = false;
  } catch (e) {
    alert(`getUserMedia() error: ${e.name}`);
  }
}

getSelectedSdpSemantics = () => {
//   const sdpSemanticsSelect = document.querySelector('#sdpSemantics');
//   const option = sdpSemanticsSelect.options[sdpSemanticsSelect.selectedIndex];
//   return option.value === '' ? {} : {sdpSemantics: option.value};
  //返回默认Default
  return {};
}

 call = async ()  => {
//   callButton.disabled = true;
//   hangupButton.disabled = false;
  console.log('Starting call');
  startTime = window.performance.now();
  const videoTracks = localStream.getVideoTracks();
  const audioTracks = localStream.getAudioTracks();
  if (videoTracks.length > 0) {
    console.log(`Using video device: ${videoTracks[0].label}`);
  }
  if (audioTracks.length > 0) {
    console.log(`Using audio device: ${audioTracks[0].label}`);
  }
  const configuration = this.getSelectedSdpSemantics();
  console.log('RTCPeerConnection configuration:', configuration);
  pc1 = new RTCPeerConnection(configuration);
  console.log('Created local peer connection object pc1');
  pc1.addEventListener('icecandidate', (e) => this.onIceCandidate(pc1, e));
  pc2 = new RTCPeerConnection(configuration);
  console.log('Created remote peer connection object pc2');
  pc2.addEventListener('icecandidate', (e) => this.onIceCandidate(pc2, e));
//   pc1.addEventListener('iceconnectionstatechange', (e) => this.onIceStateChange(pc1, e));
//   pc2.addEventListener('iceconnectionstatechange', (e) => this.onIceStateChange(pc2, e));
  pc2.addEventListener('track', this.gotRemoteStream);

  localStream.getTracks().forEach(track => pc1.addTrack(track, localStream));
  console.log('Added local stream to pc1');

  try {
    console.log('pc1 createOffer start');
    const offer = await pc1.createOffer(offerOptions);
    await this.onCreateOfferSuccess(offer);
  } catch (e) {
    this.onCreateSessionDescriptionError(e);
  }
}

onCreateSessionDescriptionError = (error) => {
  console.log(`Failed to create session description: ${error.toString()}`);
}

onCreateOfferSuccess = async (desc) => {
  console.log(`Offer from pc1\n${desc.sdp}`);
  console.log('pc1 setLocalDescription start');
  try {
    await pc1.setLocalDescription(desc);
    this.onSetLocalSuccess(pc1);
  } catch (e) {
    this.onSetSessionDescriptionError();
  }

  console.log('pc2 setRemoteDescription start');
  try {
    await pc2.setRemoteDescription(desc);
    this.onSetRemoteSuccess(pc2);
  } catch (e) {
    this.onSetSessionDescriptionError();
  }

  console.log('pc2 createAnswer start');
  // Since the 'remote' side has no media stream we need
  // to pass in the right constraints in order for it to
  // accept the incoming offer of audio and video.
  try {
    const answer = await pc2.createAnswer();
    await this.onCreateAnswerSuccess(answer);
  } catch (e) {
    this.onCreateSessionDescriptionError(e);
  }
}

onSetLocalSuccess = (pc) => {
  console.log(`${this.getName(pc)} setLocalDescription complete`);
}

onSetRemoteSuccess = (pc) => {
  console.log(`${this.getName(pc)} setRemoteDescription complete`);
}

onSetSessionDescriptionError = (error) => {
  console.log(`Failed to set session description: ${error.toString()}`);
}

gotRemoteStream = (e) => {
  if (remoteVideo.srcObject !== e.streams[0]) {
    remoteVideo.srcObject = e.streams[0];
    console.log('pc2 received remote stream');
  }
}

onCreateAnswerSuccess = async (desc) => {
  console.log(`Answer from pc2:\n${desc.sdp}`);
  console.log('pc2 setLocalDescription start');
  try {
    await pc2.setLocalDescription(desc);
    this.onSetLocalSuccess(pc2);
  } catch (e) {
    this.onSetSessionDescriptionError(e);
  }
  console.log('pc1 setRemoteDescription start');
  try {
    await pc1.setRemoteDescription(desc);
    this.onSetRemoteSuccess(pc1);
  } catch (e) {
    this.onSetSessionDescriptionError(e);
  }
}

onIceCandidate = async (pc, event) => {
  try {
    let tmpPc =  this.getOtherPc(pc);
    await tmpPc.addIceCandidate(event.candidate)
    // await (this.getOtherPc(pc).addIceCandidate(event.candidate));
    this.onAddIceCandidateSuccess(pc);
  } catch (e) {
    this.onAddIceCandidateError(pc, e);
  }
  console.log(`${this.getName(pc)} ICE candidate:\n${event.candidate ? event.candidate.candidate : '(null)'}`);
}

onAddIceCandidateSuccess = (pc) => {
  console.log(`${this.getName(pc)} addIceCandidate success`);
}

onAddIceCandidateError = (pc, error) => {
  console.log(`${this.getName(pc)} failed to add ICE Candidate: ${error.toString()}`);
}

onIceStateChange = (pc, event) => {
  if (pc) {
    console.log(`${getName(pc)} ICE state: ${pc.iceConnectionState}`);
    console.log('ICE state change event: ', event);
  }
}

hangup = () => {
  console.log('Ending call');
  pc1.close();
  pc2.close();
  pc1 = null;
  pc2 = null;
//   hangupButton.disabled = true;
//   callButton.disabled = false;
}




  render() {
    
    return (
      <div className="sample-container">
        <video ref="localVideo" playsInline autoPlay muted></video>
     <video ref="remoteVideo" playsInline autoPlay></video>

    <div class="box">
        <Button ref="startButton" onClick={this.start}>Start</Button>
        <Button ref="callButton" onClick={this.call}>Call</Button>
        <Button ref="hangupButton" onClick={this.hangup}>Hang Up</Button>
    </div>

    <div class="box">
        <span>SDP Semantics:</span>
        <select ref="sdpSemantics">
            <option selected value="">Default</option>
            <option value="unified-plan">Unified Plan</option>
            <option value="plan-b">Plan B</option>
        </select>
    </div>
      </div>
    );
  }
}

export default PeerConnection;
